<template>
  <div class="expertInformation" v-loading.body="bodyLoading">
    <div class="mainContainer">
      <!-- <div class="title">
        <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
        <span>专家信息</span>
        <div class="new--btn" @click="handleAdd" style="float: right">+ 新建</div>
      </div> -->
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="handleAdd">+ 新建</div>
      </crumb>
      <div class="listQuery">
        <el-form ref="listQuery" :model="listQuery" label-width="90px">
          <el-row>
            <el-col :span="6">
              <el-form-item label="专家类型：">
                <el-select v-model="listQuery.type" placeholder="请选择专家类型" style="width: 90%" @keyup.enter.native="getList">
                  <el-option
                    v-for="item in options"
                    :key="item.code"
                    :label="item.value"
                    :value="item.code">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="专家姓名：">
                <el-input
                  @keyup.enter.native="getList"
                  placeholder="请输入专家姓名"
                  style="width: 90%"
                  v-model="listQuery.name">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <div class="search--btn"  @click="getList">搜索</div>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div class="tableBody">
        <!-- <div class="buttonGroup">
          <el-button type="primary" round size="small" icon="el-icon-plus" style="float: right" @click="handleAdd">新增</el-button>
          <div @click="handleExport" icon="el-icon-plus" style="float: right; margin-right: 20px; color: #5e77ff; cursor: pointer;"><img src="@/assets/dao.png" alt="" style="float: left;margin: 8px 5px 0 0"> 导入</div>
        </div> -->
        <el-table
          :data="tableData"
          stripe
          style="width: 100%">
          
          <el-table-column
            prop="name"
            label="专家姓名">
          </el-table-column>
          <el-table-column
            prop="age"
            label="年龄">
          </el-table-column>
          <el-table-column
            prop="subordinateUnits"
            label="就职单位">
          </el-table-column>
          <el-table-column
            prop="typeName"
            label="专家类型">
          </el-table-column>
          <el-table-column
            prop="phone"
            label="手机号">
          </el-table-column>
          <el-table-column
            prop="email"
            label="邮箱">
          </el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="listQuery.page"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="listQuery.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑弹框 -->
    <el-dialog :title="title" :visible.sync="dialogFormVisible" :close-on-click-modal='false' width="40%">
      <el-form :model="form" :rules="rules" ref="form"  label-width="120px">
        <el-form-item label="专家类型：" prop="type">
          <el-select v-model="form.type" placeholder="请选择专家类型" size="small" style="width: 100%">
            <el-option
              v-for="item in options1"
              :key="item.code"
              :label="item.value"
              :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="专家姓名：" prop="name">
          <el-input v-model="form.name" auto-complete="off" placeholder="请输入专家姓名" size="small"></el-input>
        </el-form-item>
        <el-form-item label="所属单位：" prop="subordinateUnits">
          <el-input v-model="form.subordinateUnits" auto-complete="off" placeholder="请输入所属单位" size="small"></el-input> 
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="年龄：" prop="age">
              <el-input v-model.number="form.age" auto-complete="off" placeholder="请输入年龄" size="small"></el-input> 
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="性别：" prop="sex">
              <el-select v-model="form.sex" placeholder="请选择性别" size="small" style="width: 100%">
                <el-option
                  v-for="item in sexOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="邮箱：" prop="email">
          <el-input v-model="form.email" auto-complete="off" placeholder="请输入邮箱" size="small"></el-input> 
        </el-form-item>
        <el-form-item label="手机号：" prop="phone">
          <el-input v-model="form.phone" auto-complete="off" placeholder="请输入手机号" size="small"></el-input> 
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')" v-show="title==='添加专家信息'" :loading="loading">确 定</el-button>
        <el-button type="primary" @click="editForm('form')" v-show="title!=='添加专家信息'" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Crumb from '@/components/Crumb'
export default {
  components: { Crumb },
  data() {
    const validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback();
      } else {
        let reg = /^[1][3,4,5,7,8][0-9]{9}$/
        if(!reg.test(value)){
          callback(new Error('请输入正确的手机号码'))
        }else{
          callback()
        }
      }
    }
    return {
      icon: 'el-icon-refresh',
      formLabelWidth: '120px',
      dialogFormVisible: false,
      dialogVisible: false,
      uploadDisabled: false,
      uploadUrl: '',
      active: 1,
      listQuery: {
        type: '',
        name: '',
        page: 1,
        rows: 15
      },
      form: {
        type: '',
        name: '',
        subordinateUnits: '',
        age: '',
        sex: '',
        email: '',
        phone: ''
      },
      options: [
         {
          code:'',
          value:'全部'
        },
         {
          code:1,
          value:'校内专家'
        },
         {
          code:2,
          value:'校外专家'
        }
      ],
      options1: [
        {
          code:1,
          value:'校内专家'
        },
         {
          code:2,
          value:'校外专家'
        }
      ],
      sexOptions: [
        {
          value: 0,
          label: '男'
        },
        {
          value: 1,
          label: '女'
        }
      ],
      tableData: [],
      total: 0,
      title: '添加专家信息',
      rules: {
        type: [
          { required: true, message:'请选择专家类型', trigger: 'change' }
        ],
        name: [
          { required: true, message:'请输入专家名称', trigger: 'blur' }
        ],
        subordinateUnits: [
          { required: true, message:'请输入所属单位', trigger: 'blur' }
        ],
        age: [
          { required: true, message:'请输入年龄', trigger: 'blur' },
          { type: 'number', message: '年龄必须为数字值'}
        ],
        sex: [
          { required: true, message:'请选择性别', trigger: 'change' }
        ],
        email: [
          { required: true, message:'请输入联系人邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
        ],
        phone: [
          { required: true, message:'请输入联系人手机号', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' }
        ]
      },
      loading: false,
      bodyLoading: true
    }
  },
  mounted() {
    this.getList()
    this.getEnterprise()
  },
  methods: {
    resetListQuery() {
      this.listQuery = {
        type: '',
        name: '',
        page: 1,
        rows: 15
      }
    },
    getList() {
      this.bodyLoading = true
      this.$post('/basic/expertInfo/list', this.listQuery).then(res => {
        if(res.status === 1) {
          this.tableData = res.data
          this.total = res.total
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取列表失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    getEnterprise() {
      this.bodyLoading = true
      this.$get('/system/dict/field/expertType').then(res => {
        if(res.status === 1) {
          this.options = Object.assign([], res.data)
          this.options.unshift({
            code: '',
            value: '全部'
          })
          this.options1 = Object.assign([], res.data)
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取专家类型失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    handleAdd() {
      this.dialogFormVisible = true
      this.title = '添加专家信息'
      this.$nextTick(() => {
        this.form = {
          type: '',
          name: '',
          subordinateUnits: '',
          age: '',
          sex: '',
          email: '',
          phone: ''
        }
        this.$refs['form'].resetFields();
      })
    },
    handleEdit(row) {
      this.dialogFormVisible = true
      this.title = '编辑专家信息'
      this.form = {
        id: row.id,
        type: row.type,
        name: row.name,
        subordinateUnits: row.subordinateUnits,
        age: row.age,
        sex: row.sex,
        email: row.email,
        phone: row.phone
      }
      this.$nextTick(() => {
        this.$refs['form'].clearValidate();
      })
    },
    handleDelete(row) {
      this.$confirm('此操作将删除该专家信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$post(`/basic/expertInfo/del/${row.id}`).then(res => {
          if(res.status === 1) {
            this.$notify({
              title: '提示',
              message: res.message,
              type: 'success',
              duration: '2000'
            })
            this.listQuery.page = 1
            this.getList()
          }else {
            this.$notify({
              title: '提示',
              message: res.message,
              type: 'error',
              duration: '2000'
            })
          }
        }).catch(() => {
          this.$notify({
              title: '提示',
              message: '删除失败！请重试！',
              type: 'error',
              duration: '2000'
            })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    handleSizeChange(val) {
      this.listQuery.rows = val
      this.getList()
    },
    handleCurrentChange(val) {
      this.listQuery.page = val
      this.getList()
    },
    submitForm(formName) {
      this.loading = true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post('/basic/expertInfo/add', this.form).then(res => {
            if(res.status === 1) {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'success',
                duration: '2000'
              })
              this.dialogFormVisible = false
              this.resetListQuery()
              this.getList()
            }else {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'error',
                duration: '2000'
              })
            }
            this.loading = false
          }).catch(() => {
            this.loading = false
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    editForm(formName) {
      this.loading = true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post('/basic/expertInfo/edit', this.form).then(res => {
            if(res.status === 1) {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'success',
                duration: '2000'
              })
              this.dialogFormVisible = false
              this.resetListQuery()
              this.getList()
            }else {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'error',
                duration: '2000'
              })
            }
            this.loading = false
          }).catch(() => {
            this.loading = false
          })
        } else {
          this.loading = false
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss">
.expertInformation {
  padding-bottom: 50px;
  .mainContainer {
    overflow: hidden;
    margin-bottom: 50px;
    .title {
      line-height: 40px;
      margin-bottom: 20px;
      span {
        font-size: 14px;
        font-weight: 700;
        color: #666;
      }
      .buttonGroup {
        margin-bottom: 10px;
        overflow: hidden;
        line-height: 32px;
        float: right;
      }
    }
    .listQuery {
      background-color: #fff;
      padding: 20px 20px 0;
      border-radius: 10px;
      margin: 0 5px 20px;
      .el-form-item__label{
        font-size:14px;
        font-family:PingFang SC;
        font-weight:bold;
        color:rgba(105,105,105,1);
      }
    }
    .tableBody {
      overflow: initial;
      border-radius: 10px 10px 0 0;
      margin: 0 5px;
      .el-table {
        .distance {
          margin-right: 10px;
        }
        .el-table__header {
          tr {
            height: 50px;
            th {
              background: #fff;
              .cell {
                color: #666;
                font-weight: 700;
              }
            }
          }
        }
        .el-table__body {
          .el-table__row {
            height: 40px;
            td {
              padding: 0;
            }
          }
        }
      }
    }
    .pagination {
      padding: 20px;
      margin: 0 5px;
      text-align: right;
      background-color: #fff;
      border-radius: 0 0 10px 10px;
    }
  }
}
</style>



